.slide {
  width: 95%;
  height: 532px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 30px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
.slideFoto {
  display: block;
  height: 100%;
  width: auto;
  margin: 0 auto;
}
.wrapperEvent {
  height: calc(100% - 177px);
  position: absolute;
  width: 270px;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  opacity: 0;
  z-index: 9999;
}
.slideItem {
  position: relative;
  padding-bottom: 177px;
}
.hover {
  position: absolute;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0);
}
.marker {
  display: block;
  height: 177px;
}
.wrapperName {
  transform: translate(10px, -68px);
}
.slideName {
  word-spacing: 100px;
  font-weight: 200;
  font-size: 24px;
  text-align: center;
  color: #222;
}
.slidePosition {
  font-size: 18px;
  font-weight: 300;
  color: #575757;
  text-align: center;
}
