.section {
  height: 690px;
  background: #f4f5f9;
  background-image: url('../../img/background/teamMobSomething.png');
  background-image: -webkit-image-set(url('../../img/background/teamDeskSomething.webp') 1x, url('../../img/background/teamDeskSomething.png') 1x);
  background-size: 27% auto;
  background-position: 100% 0;
  background-repeat: no-repeat;
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 97%, 50% 100%, 0 97%);
  transform: translate(0, -39%);
  -ms-transform: translate(0, 0);
  padding-top: 40px;
}
.header {
  font-size: 30px;
  color: #222222;
  text-transform: uppercase;
  font-family: 'Poiret One';
  text-align: center;
  line-height: 1;
  margin-bottom: 30px;
}

@media(min-width: 1200px) {
  .section {
    height: 886px;
    background-image: url('../../img/background/teamDeskSomething.png');
    background-image: -webkit-image-set(url('../../img/background/teamDeskSomething.webp') 1x, url('../../img/background/teamDeskSomething.png') 1x);
    background-size: auto 31%;
    background-position: 0 0;
    background-repeat: no-repeat;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 94%, 50% 100%, 0 94%);
    transform: translate(0, -78%);
    -ms-transform: translate(0, 0);
    padding-top: 94px;
    position: relative;
  }
  .header {
    font-size: 60px;
    height: 84px;
    line-height: 84px;
    background: url('../../img/elements/buttonsGroup.png');
    background-image: -webkit-image-set(url('../../img/elements/buttonsGroup.webp') 1x, url('../../img/elements/buttonsGroup.png') 1x);
    background-repeat: no-repeat;
    background-position: 40% 0;
    margin-bottom: 35px;
  }
}
