.slide {
  height: 500px;
  display: grid;
  width: 320px;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
.slideItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.slideFoto {
  display: block;
  margin-bottom: 15px;
}
.slideName {
  font-size: 14px;
  text-align: center;
  font-weight: 300;
  color: #222222;
  line-height: 1;
  margin-bottom: 10px;
}
.slidePosition {
  font-size: 12px;
  font-weight: 300;
  color: #575757;
  text-align: center;
}
